.slide-comment .close-comment {
  position: absolute;
  right: 15px;
  top: 10px;
  width: 28px;
  height: 28px;
  border-radius: 5px;
  background: #fff;
  display: none;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.slide-comment .close-comment svg {
  width: 26px;
  height: 26px;
}
@media (max-width: 991px) {
  .slide-comment {
    position: fixed;
    background: #fff;
    top: 0;
    bottom: 0;
    right: -100%;
    width: 100% !important;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    z-index: 1001;
  }
  .slide-comment .title-cate {
    text-align: left;
  }
  .slide-comment .title-cate div {
    display: flex;
    align-items: center;
    gap: 4px;
    background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .slide-comment .title-cate div .ic {
    background: url(images/graphics/ic-thaoluan.png) no-repeat;
    width: 20px;
    height: 21px;
  }
  .slide-comment .close-comment {
    display: flex;
  }
  .slide-comment .input-mobile {
    width: 100%;
    background: var(--bg-main);
    border-top: 1px solid #dbe2df;
    position: sticky;
    top: 100dvh;
    z-index: 2;
  }
  .slide-comment .input-mobile .input-comment::before {
    left: 10px;
  }
  .slide-comment .input-mobile .form-control {
    border: 1px solid #DBE2DF;
    font-size: 14px;
    width: calc(100% - 65px);
  }
  .slide-comment .list-comment {
    height: calc(100dvh - 225px);
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: #d0d0d0 #f9f9f9;
    padding-bottom: 20px;
    margin-top: -72px;
  }
  .slide-comment.overflow-y {
    overflow-y: scroll;
  }
  .slide-comment.open {
    right: 0;
  }
}

.input-comment {
  position: relative;
}
.input-comment::before {
  content: "";
  background: url(images/icons/ic-comment.png);
  position: absolute;
  width: 24px;
  height: 24px;
  top: 8px;
  left: 0;
}
.input-comment .form-control {
  border: 0;
  padding: 8px 60px 8px 40px;
  height: 40px;
}
.input-comment .send {
  position: absolute;
  top: 0;
  right: 0;
  background: #034C31;
  border: 0;
  border-radius: 4px;
  height: 40px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.list-comment .itemt-comment {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 20px;
}
.list-comment .itemt-comment:last-child {
  margin-bottom: 0;
}
.list-comment .itemt-comment .full_content {
  margin-bottom: 5px;
}
.list-comment .itemt-comment .name {
  font-weight: 500;
  font-size: 16px;
  margin-bottom: 5px;
}
.list-comment .itemt-comment .block_like_web {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.list-comment .itemt-comment .block_like_web .time {
  color: #ABB0AE;
  font-weight: 300;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap {
  display: flex;
  align-items: center;
  gap: 8px;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap a {
  color: #656867;
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 300;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap a .ic-reply {
  background: url(images/icons/ic-reply.svg);
  width: 18px;
  height: 18px;
  background-size: contain;
  margin-top: -3px;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap a .ic-like {
  background: url(images/icons/ic-like.svg);
  width: 18px;
  height: 18px;
  background-size: contain;
  margin-top: -3px;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap a.active {
  color: #034C31;
}
.list-comment .itemt-comment .block_like_web .reactions-wrap a.active .ic-like {
  background: url(images/icons/ic-like2.png);
  background-size: contain;
}
.list-comment .itemt-comment .sub_comment {
  border-left: 2px dashed #DBE2DF;
  padding-left: 15px;
}
.list-comment .itemt-comment .sub_comment .sub_comment_item {
  width: 100%;
  margin-top: 15px;
}
.list-comment .pagination {
  border-top: 1px solid #dbe2df;
  padding-top: 20px;
  margin-left: -30px;
  margin-right: -30px;
}
@media (max-width: 1480px) {
  .list-comment .itemt-comment {
    font-size: 14px;
  }
  .list-comment .pagination {
    margin-left: -20px;
    margin-right: -20px;
  }
}
@media (max-width: 600px) {
  .list-comment .pagination {
    margin-left: 0;
    margin-right: 0;
    padding-top: 15px;
  }
}